<template>
  <div class="jh_purchase">
    <!--  -->
    <div>
      <returnHome :title="title"></returnHome>
    </div>
    <!--  -->
    <!--  -->
    <div class="jh_shop">
      <!--  -->
      <div class="jh_shop_title">
        <div class="jh_shop_title_cont">
          <p>{{info.title}}</p>
          <p>:{{info.teacher_name}}</p>
        </div>
      </div>
      <!--  -->

      <!--  -->
      <div class="jh_coupon">
        <p></p>
        <p>{{info.has_coupon===0?'':''}}</p>
      </div>
      <!--  -->

      <!--  -->
      <div class="jh_price">
        <div>
          <p></p>
          <p>
            <img src="@/assets/20191HHDExgz0u1567065946.png" alt />
            <span>{{info.order_price|toFixPrice}}</span>
          </p>
        </div>

        <div>
          <p></p>
          <p>
            <img src="@/assets/20191HHDExgz0u1567065946.png" alt />
            <span>{{info.has_coupon}}</span>
          </p>
        </div>

        <div>
          <p></p>
          <p>
            <img src="@/assets/20191HHDExgz0u1567065946.png" alt />
            <span style="color:#e02020">{{info.price|toFixPrice}}</span>
          </p>
        </div>
      </div>
      <!--  -->
    </div>
    <!--  -->

    <!--  -->
    <div class="jh_add">
      <p>
        <span></span>
        <img src="@/assets/20191HHDExgz0u1567065946.png" alt />
        <span>{{info.price|toFixPrice}}</span>
      </p>
      <p @click="shopPay"></p>
    </div>
    <!--  -->
  </div>
</template>

<script>
import returnHome from "../../components/return/return";
import { shopInfo, shopPay } from "../../http/api";
import Vue from "vue";
import { Dialog } from "vant";
export default {
  components: {
    returnHome
  },
  data() {
    return {
      id: "", //id
      course_type: "", //
      title: "", //,
      checked_coupon: {}, //
      info: {} //
    };
  },
  created() {
    this.title = this.$route.meta.title;
    this.id = this.$route.query.id;
    this.course_type = this.$route.query.course_type;
  },
  mounted() {
    this.shopInfo();
  },
  methods: {
    // 
    shopInfo() {
      let token = localStorage.getItem("token");
      return shopInfo(token, this.id, this.course_type).then(res => {
        this.info = res.data.data.info;
        this.checked_coupon = res.data.data.info.checked_coupon;
      });
    },
    // 
    shopPay() {
      let token = localStorage.getItem("token");
      return shopPay(
        token,
        this.id,
        this.course_type,
        this.checked_coupon.coupon_id
      ).then(res => {
        if (res.data.code === 200) {
          Dialog.confirm({
            title: "",
            message: "",
            confirmButtonText	:''
          })
            .then(() => {
                this.$router.push('/myMoney')
            })
            .catch(() => {
            });
        }
      });
    }
  }
};
</script>

<style scoped lang='scss'>
.jh_shop {
  width: 100%;
}
// 
.jh_shop_title {
  width: 100%;
  padding: 30px;
  box-sizing: border-box;
  .jh_shop_title_cont {
    border-radius: 10px;
    background: #fff;
    padding: 40px 30px;
    box-sizing: border-box;
    font-size: 30px;
    p {
      line-height: 50px;
    }

    p:nth-last-of-type(1) {
      color: #b7b7b7;
    }
  }
}
// 

// 
.jh_coupon {
  display: flex;
  justify-content: space-between;
  height: 95px;
  align-items: center;
  padding: 0px 30px;
  box-sizing: border-box;
  background: #fff;
  font-size: 30px;
  p:nth-last-of-type(1) {
    color: #b7b7b7;
  }
}
// 

// 
.jh_price {
  width: 100%;
  background: #fff;
  padding: 30px 0px;
  box-sizing: border-box;
  margin-top: 30px;
  > div {
    display: flex;
    justify-content: space-between;
    font-size: 28px;
    color: #b7b7b7;
    padding: 0px 30px;
    box-sizing: border-box;
    line-height: 40px;

    img {
      width: 25px;
      height: 25px;
      margin-right: 10px;
    }
  }
}
// 
.jh_add {
  position: fixed;
  bottom: 0px;
  left: 0;
  right: 0;
  height: 110px;
  background: #fff;
  display: flex;
  justify-content: space-between;
  p:nth-of-type(1) {
    width: 60%;
    img {
      width: 21px;
      height: 25px;
      margin: 0px 10px;
    }
    display: flex;
    align-items: center;
    padding: 0px 0px 0px 50px;
    box-sizing: border-box;
    span {
      font-size: 25px;
      color: #b7b7b7;
    }
    span:nth-of-type(2) {
      font-size: 40px;
      color: #e02020;
    }
  }
  p:nth-of-type(2) {
    width: 40%;
    background: #eb6100;
    color: #fff;
    text-align: center;
    line-height: 110px;
  }
}
</style>
